      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span> <span class="hljs-attr">:geometry-instances.sync</span>=<span class="hljs-string">"geometryInstances"</span>&gt;</span>
