<h2>属性</h2>
<h3><code v-pre="">vc-geometry-polyline-volume</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>polylinePositions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定 polyline volume 位置信息。 <strong>struct: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>shapePositions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定 polyline volume 拉伸的形状数组。 <strong>struct: [{ x: number, y: number },...,{ x: number, y: number }]</strong></td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polyline volume 参考椭球体。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polyline volume 每个经纬度之间的距离（弧度）。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polyline volume 顶点属性渲染方式。</td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polyline volume 转角类型。 <strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre="">vc-geometry-outline-polyline-volume</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>polylinePositions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定 polyline volume 位置信息。 <strong>struct: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>shapePositions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定 polyline volume 拉伸的形状数组。 <strong>struct: [{ x: number, y: number },...,{ x: number, y: number }]</strong></td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polyline volume 参考椭球体。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polyline volume 每个经纬度之间的距离（弧度）。</td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polyline volume 转角类型。 <strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PolylineGeometry.html">PolylineGeometry</a></strong>、 <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PolylineVolumeOutlineGeometry.html">PolylineVolumeOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
